<template>
  <home-container :title="step === 1 ? 'Send':null" default-href="/home/wallet" :with-header="step === 5 ? false : true">
    <div v-if="step === 1">
      <div class="row mb-3">
        <div class="col-8 mx-auto text-center">
          <ion-text
            ><p class="font-weight-light">
              Who would you like to send to
            </p></ion-text
          >
        </div>
      </div>
      <ion-label position="stacked">Add Address</ion-label>
      <ion-item class="border" lines="none">
        <ion-input
          v-model="username"
          @ionBlur="searchUsername"
          :clear-input="true"
          placeholder="Enter recepient's wallet address"
        ></ion-input>
      </ion-item>
      <ion-button strong class="ion-margin-horizontal my-4" expand="block">
        <ion-label>Scan QR Code</ion-label>
      </ion-button>
      <ion-button strong
        @click.prevent="nextStep"
        v-if="recepient.name"
        class="ion-margin-horizontal"
        expand="block"
      >
        <ion-label>Next</ion-label>
      </ion-button>
    </div>
    <div v-if="step === 2">
      <ion-item lines="none" class="ion-no-padding">
        <ion-button color="dark" slot="start" fill="clear" @click.prevent="previousStep">
          <ion-icon slot="icon-only" :icon="arrowBackOutline"></ion-icon>
        </ion-button>
        <ion-text slot="start"><h1 class="">Send</h1></ion-text>
      </ion-item>
      <div class="row mb-3">
        <div class="col-8 mx-auto text-center">
          <ion-text
            ><p class="font-weight-light">
              How much do you want to send to
              <strong class="text-uppercase">{{ recepient.name }}</strong>
            </p></ion-text
          >
        </div>
      </div>
      <ion-label>Amount</ion-label>
      <ion-item class="border ion-margin-vertical" lines="none">
        <ion-label style="padding-right: 10px; border-right: 1px solid #e0e5eb"
          >BTC</ion-label
        >
        <ion-input type="number"
          v-model.lazy="amount.in_btc"
          placeholder="Enter amount here"
        ></ion-input>
      </ion-item>
      <ion-item class="border ion-margin-vertical" lines="none">
        <ion-label style="padding-right: 10px; border-right: 1px solid #e0e5eb"
          >USD</ion-label
        >
        <ion-input type="number" v-model.lazy="amount.in_usd"></ion-input>
      </ion-item>
      <ion-label>Note<em class="small text-muted"> (Optional)</em></ion-label>
      <ion-item class="border ion-margin-vertical" lines="none">
        <ion-textarea
          v-model="amount.note"
          placeholder="Leave a note for the recepient"
        ></ion-textarea>
      </ion-item>
      <ion-button strong
        :disabled="!amount.in_btc && !amount.in_usd"
        @click.prevent="nextStep"
        class="ion-margin-horizontal my-4"
        expand="block"
      >
        <ion-label>Send</ion-label>
      </ion-button>
    </div>
    <div v-if="step === 3">
      <ion-item lines="none" class="ion-no-padding">
        <ion-button color="dark" slot="start" fill="clear" @click.prevent="previousStep">
          <ion-icon slot="icon-only" :icon="arrowBackOutline"></ion-icon>
        </ion-button>
        <ion-text slot="start"><h1 class="">Confirmation</h1></ion-text>
      </ion-item>
      <ion-list>
        <ion-item lines="full">
          <ion-label slot="start"><small>Currency</small></ion-label>
          <ion-label slot="end">Bitcoin (BTC)</ion-label>
        </ion-item>
        <ion-item lines="full">
          <ion-label slot="start"><small>Amount</small></ion-label>
          <ion-label slot="end">{{ amount.in_btc.toPrecision(8) }}</ion-label>
        </ion-item>
        <ion-item lines="full">
          <ion-label slot="start"><small>Transaction fee</small></ion-label>
          <ion-label slot="end">10 USD</ion-label>
        </ion-item>
        <ion-item lines="full">
          <ion-label slot="start"><small>Note</small></ion-label>
          <ion-label slot="end" class="ion-text-wrap">{{
            amount.note
          }}</ion-label>
        </ion-item>
      </ion-list>
      <ion-button strong
        class="ion-margin-horizontal my-4"
        expand="block"
        @click.prevent="nextStep"
      >
        <ion-label>Confirm</ion-label>
      </ion-button>
    </div>
    <div v-if="step === 4">
      <ion-item lines="none" class="ion-no-padding">
        <ion-button color="dark" slot="start" fill="clear" @click.prevent="previousStep">
          <ion-icon slot="icon-only" :icon="arrowBackOutline"></ion-icon>
        </ion-button>
        <ion-text slot="start"><h1 class="">Input OTP</h1></ion-text>
      </ion-item>
      <div class="row my-3">
        <div class="col-9 mx-auto text-center">
          <ion-text><p class="font-weight-light">
              To complete transaction,<br />
              Input OTP code sent to +234*******484
            </p></ion-text>
        </div>
      </div>
      <div class="d-flex justify-content-center">
        <v-otp-input  @on-complete="handleOnComplete" inputClasses="otp-input" :numInputs="6" separator=" "></v-otp-input>
      </div>
      <div class="row my-4">
        <div class="col-9 mx-auto text-center">
          <ion-text
            ><p class="">
              Code Expires in,<br />
              <strong>{{ countdown }}</strong>
            </p></ion-text
          >
        </div>
      </div>
      <ion-button strong
        :disabled="!authorized"
        @click.prevent="processSend"
        class="ion-margin-horizontal my-4"
        expand="block"
      >
        <ion-label>Authorize</ion-label>
      </ion-button>
      <div class="my-4">
        <ion-text
          ><p class="d-flex flex-column align-items-end justify-content-end">
            <ion-text>Didn't get the code?</ion-text>
            <a href='' @click.prevent="countDownTick" class="strong"
              >Request another code</a
            >
          </p></ion-text
        >
      </div>
    </div>
    <div
      style="height: 100vh"
      class="d-flex flex-column align-items-center justify-content-center"
      v-if="step === 5"
    >
      <ion-icon
        class="icon-container mb-5"
        style="font-size: 4rem"
        :icon="checkmarkOutline"
      ></ion-icon>
      <ion-text><h1 class="">Congratulations</h1></ion-text>
      <p class='lead text-center'>
        Your transaction to <strong>{{ recepient.name }}</strong> was
        successfully
      </p>
      <ion-button strong
        @click.prevent="router.push('/home/dashboard')"
        class="ion-margin-horizontal my-4 w-100"
        expand="block"
      >
        <ion-label>Home</ion-label>
      </ion-button>
    </div>
  </home-container>
</template>
<script>
import { defineComponent } from "vue";

import {
  closeOutline,
  arrowBackOutline,
  checkmarkOutline,
} from "ionicons/icons";
import { useRouter } from "vue-router";
import { loadingController } from "@ionic/vue";
import VOtpInput from 'vue3-otp-input';
export default defineComponent({
  components: {VOtpInput},
  data() {
    return {
      username: null,
      recepient: {
        name: null,
        wallet_id: null,
      },
      amount: {
        in_btc: null,
        in_usd: null,
        note: null,
      },
      step: 1,
      countdown: null,
      timer: null,
      disableAuthorization: true,
      otp:null,
    };
  },
  computed:{
      authorized(){
          return this.otp && !this.disableAuthorization
      }
  },
  watch: {
    "amount.in_btc"(value) {
      this.amount.in_usd = 47186.2 * value;
    },
    "amount.in_usd"(value) {
      this.amount.in_btc = value / 47186.2;
    },
  },
  async ionViewDidLeave() {
    this.resetFields();
    this.username = null;
  },
  setup() {
    const router = useRouter();
    return {
      closeOutline,
      arrowBackOutline,
      checkmarkOutline,
      router,
    };
  },
  methods: {
    resetFields() {
      this.recepient = { name: null, wallet_id: null };
      this.amount = { in_btc: null, in_usd: null, note: null };
      this.step = 1;
      this.countdown = null;
      this.timer = null;
      this.disableAuthorization = true;
      this.otp = null;
    },
    closePage(returnRoute) {
      this.router.push(returnRoute);
    },
    nextStep() {
      this.step++;
      clearInterval(this.timer);
      if (this.step === 4) {
        this.countDownTick();
      }
    },
    previousStep() {
      this.step--;
      this.timer = null;
      this.disableAuthorization = true;
      this.otp = null;

    },
    async searchUsername() {
      if (!this.username || this.username.length < 2) {
        this.resetFields();
        return;
      }
      let loading = await loadingController.create({
        message: "Please wait...",
      });
      loading.present();
      setTimeout(() => {
        this.recepient = {
          name: "Eiremiokhae Stanley",
          wallet_id: window.crypto.randomUUID(),
        };
        loading.dismiss();
      }, 1000);
    },
    countDownTick() {
      clearInterval(this.timer);
      this.disableAuthorization = false;
      let countDownDate = new Date().setMinutes(new Date().getMinutes() + 1);
      this.timer = setInterval(() => {
        var now = new Date().getTime();
        var distance = countDownDate - now;
        var minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
        var seconds = Math.floor((distance % (1000 * 60)) / 1000);
        minutes = minutes < 10 ? "0" + minutes : minutes;
        seconds = seconds < 10 ? "0" + seconds : seconds;
        if (distance < 2) {
          clearInterval(this.timer);
          this.disableAuthorization = true;
          return;
        }
        this.countdown = minutes + ":" + seconds;
      }, 1000);
    },
    handleOnComplete($event){
        this.otp = $event;
        this.disableAuthorization = false;
        this.processSend();
    },
    async processSend(){
        let loading = await loadingController.create({
            message: "Please wait...",
        });
        loading.present();
        setTimeout(() => {
            this.step++;
            loading.dismiss();
        }, 1000);
    }
  },
});
</script>
